import * as Sentry from '@sentry/react';
import { ConnectedRouter } from 'connected-react-router';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import React from 'react';
import ReactDOM from 'react-dom';
import ReactGA from 'react-ga4';
import { Provider } from 'react-redux';
import Router from './Router';
import configureStore, { history } from './configureStore';
import rootSaga from './sagas/root';
import * as serviceWorker from './serviceWorker';

// import Global CSS
import { Modal } from 'antd';
import './App.css';
import { getAppUrl, getServerConnection } from './utils/helper/utilities';

dayjs.extend(relativeTime);

const store = configureStore();
store.runSaga(rootSaga);

ReactGA.initialize('G-3YXW8S2K7X');

Sentry.init({
	dsn: 'https://73f7311b01963a67d542ca965e0912a0@o4507625496969216.ingest.us.sentry.io/4507625601433600',
	integrations: [Sentry.browserTracingIntegration(), Sentry.replayIntegration()],
	// Performance Monitoring
	tracesSampleRate: 1.0, //  Capture 100% of the transactions
	// Set 'tracePropagationTargets' to control for which URLs distributed tracing should be enabled
	tracePropagationTargets: ['localhost', getAppUrl()],
	// Session Replay
	replaysSessionSampleRate: 0.1, // This sets the sample rate at 10%. You may want to change it to 100% while in development and then sample at a lower rate in production.
	replaysOnErrorSampleRate: 1.0 // If you're not already sampling the entire session, change the sample rate to 100% when sampling sessions where errors occur.
});

ReactDOM.render(
	<Provider store={store}>
		<ConnectedRouter history={history}>
			<Router history={history} />
		</ConnectedRouter>
	</Provider>,
	document.getElementById('root'),
	document.addEventListener('DOMContentLoaded', getServerConnection),
	document.addEventListener('contextmenu', (e) => {
		e.preventDefault();
		Modal.error({
			width: 500,
			title: 'PERHATIAN !!!',
			content: (
				<p className="text-base leading-snug text-gray-800">
					Kami mendeteksi bahwa anda ingin mengakes fitur yang dilarang. <b className="text-red-500 uppercase">Anda tidak diizinkan</b>{' '}
					untuk mengakes fitur tersebut.
				</p>
			)
		});
		return false;
	}),
	document.addEventListener('keydown', (e) => {
		if (
			// e.keyCode === 116 ||
			// e.keyCode === 123 ||
			e.button === 23 ||
			(e.ctrlKey && e.keyCode === 116) ||
			(e.ctrlKey && e.shiftKey && e.keyCode === 73) ||
			(e.ctrlKey && e.shiftKey && e.keyCode === 80) ||
			(e.ctrlKey && e.keyCode === 85) ||
			(e.shiftKey && e.keyCode === 81)
		) {
			e.preventDefault();
			Modal.error({
				width: 500,
				title: 'PERHATIAN !!!',
				content: (
					<p className="text-base leading-snug text-gray-800">
						Kami mendeteksi bahwa anda ingin mengakes fitur yang dilarang. <b className="text-red-500 uppercase">Anda tidak diizinkan</b>{' '}
						untuk mengakes fitur tersebut.
					</p>
				)
			});
			return false;
		}
	})
);

serviceWorker.register();
